import axios from "axios";

import { createAsyncThunk } from "@reduxjs/toolkit";

axios.defaults.baseURL = "какой-то URL снова же - нужно сделать правильный";

// регистрация пользователя
const register = createAsyncThunk("auth/register", async (credentials) => {
  try {
    const { data } = await axios.post("users/signup", credentials); //установить правильный адрес. Этот для примера
    //token.set(data.token)
    return data;
  } catch (error) {
    //добавить обработку ошибки
  }
});

//логин пользователя
export const logIn = createAsyncThunk("auth/login", async (credentials) => {
  console.log("credentials ", credentials);
  try {
    const request = await axios.post(
      "http://127.0.0.1:8000/api/v1/token/", //установить правильный адрес. Этот для примера
      credentials
    );
    //token.set(data.token)
    return request;
  } catch (error) {
    //добавить обработку ошибки
  }
});

